<template>
  <div class="home-wrap">
    <HomeUserInfo
      :userInfo="user"
    ></HomeUserInfo>
    <HomeMain></HomeMain>
  </div>
</template>

<script>
import HomeUserInfo from './home-user-info'
import HomeMain from './home-main'
import {getLocalStorage} from '../../lib/util'
export default {
  name: 'Home',
  data () {
    return {
      user: ''
    }
  },
  created () {
    this.getUser()
  },
  methods: {
    getUser () {
      this.$set(this, 'user', JSON.parse(getLocalStorage('userInfo')))
    }
  },
  components: {
    HomeUserInfo,
    HomeMain
  }
}
</script>

<style lang="less" scoped>
  .home-wrap{
    height: 100%;
    background-color: #e5e5e5;
  }
</style>



