<template>
  <div class="main-wrap">
    <div class="menu-box item">
      <a href="javascript:void (0)" @click="check"><i class="fa fa-check-circle" aria-hidden="true"></i>签到</a>
      <router-link to="/message"><i class="fa fa-commenting" aria-hidden="true"></i>消息</router-link>
      <router-link to="/vehicle"><i class="fa fa-car" aria-hidden="true"></i>车辆</router-link>
      <router-link to="/order"><i class="fa fa-pie-chart" aria-hidden="true"></i>订单</router-link>
      <router-link to="/collect"><i class="fa fa-star" aria-hidden="true"></i>收藏</router-link>
      <!--<router-link to="/carte"><i class="fa fa-address-card" aria-hidden="true"></i>名片</router-link>-->
      <router-link to="/address"><i class="fa fa-location-arrow" aria-hidden="true"></i>收货地址</router-link>
      <router-link to="/home/card"><i class="fa fa-credit-card-alt" style="font-size:16px;" aria-hidden="true"></i>我的券包</router-link>
      <router-link to="/tasks"><i class="fa fa-tasks" aria-hidden="true"></i>任务中心</router-link>
      <router-link to="/dealer"><i class="fa fa-black-tie" aria-hidden="true"></i>核销中心</router-link>
    </div>
  </div>
</template>

<script>
import {checkInForum} from '../../api/forum'
export default {
  name: 'Home-Main',
  methods: {
    check () {
      checkInForum()
        .then(data => {
          this.$toast(data.errormsg)
        })
    }
  }
}
</script>

<style lang="less" scoped>
  .main-wrap{
    min-height: 100%;
    padding-bottom: 10px;
    &:after, :before{
      box-sizing: border-box;
    }
  }
  .item {
    padding: .6rem;
    margin: 1rem 0;
    background: #fff;
    a {
      color: #333;
      display: block;
      &:hover{
        background-color: #eee;
      }
      i {
        font-size: 20px;
        color: forestgreen;
        margin-right: 1rem;
      }
    }
  }
  .order-box {
    a {
      padding: .5rem 0;
    }
  }
  .menu-box {
    padding-top: 0;
    padding-bottom: 0;
    a {
      padding: .2rem 0;
      line-height: 2rem;
      border-bottom: 1px solid #eee;
    }
  }
</style>



