<template>
  <div class="user-wrap" @click="goTo('/home/user')">
    <div class="avatar">
      <img :src="userInfo.headimgurl || '/static/img/favicon.jpg'" :class="{'start': userInfo}">
    </div>
    <div class="infos">
      <h2 style="margin-bottom: 0;">{{userInfo.nickname}}</h2>
      <p style="margin: 0;margin-left: 1rem;">
        <img
          v-for="(item, index) in userInfo.medal"
          :key="index"
          :src="item.imgurl"
          :title="item.name"
          :alt="item.content"
          style="cursor: pointer; width: 22px;height: 33.25px;vertical-align: middle;"
        >
      </p>
      <span>用户ID:{{userInfo.invite_code}}</span>
    </div>
    <div class="icon-r">
      <i class="fa fa-angle-right" aria-hidden="true"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home-User-Info',
  props: {
    userInfo: {type: Object}
  },
  data () {
    return {
      popupVisible: true
    }
  },
  methods: {
    goTo (path) {
      this.$router.replace(path)
    },
    setA () {
      alert(1)
      this.a = !this.a
    }
  }
}
</script>

<style lang="less" scoped>
  .user-wrap{
    display: flex;
    padding: .6rem;
    background: #fff;
    .icon-r{
      line-height: 5rem;
      color: #999;
      font-size: 2rem;
    }
    .avatar{
      padding: .5rem;
      img {
        width: 5rem;
        height: 5rem;
        transition: all 1s ease-in-out;
      }
    }
    .infos{
      flex: 1;
      h2{
        font-weight: 500;
        margin: 1rem;
      }
      span{
        margin-left: 1rem;
        color: #999;
      }
    }
  }
  .start{
    transform: rotate(360deg);
  }
</style>



